import "./styles.css";

var operand_1 = 0;
var operand_2 = null;
var operator = null;

var display = document.querySelector("#display");
var buttons = document.getElementsByClassName("button");

function evaluate(operand_1, operand_2, operator) {
  if (operator === null || operand_2 === null) {
    return;
  }
  return eval(operand_1 + operator + operand_2);
}

var operator_next = false;

for (var i = 0; i < buttons.length; i++) {
  buttons[i].addEventListener("click", function (event) {
    var value = event.target.getAttribute("data-value");
    var temp;
    if (value === "AC") {
      display.innerHTML = "";
    } else if (value === "+/-") {
      temp = parseFloat(display.innerHTML);
      temp = -1 * temp;
      display.innerHTML = "" + temp;
    } else if (value === "%") {
      temp = parseFloat(display.innerHTML);
      temp = temp / 100;
      display.innerHTML = "" + temp;
    } else if (
      value === "/" ||
      value === "*" ||
      value === "-" ||
      value === "+"
    ) {
      if (display.innerHTML === "") {
        window.alert("Error");
        operand_1 = 0;
        operand_2 = null;
        operator = null;
      } else if (operand_2 === null) {
        operator_next = true;
        operand_1 = display.innerHTML;
        operator = value;
      } else {
        operator_next = true;
        display.innerHTML = evaluate(operand_1, operand_2, operator);
        operand_1 = display.innerHTML;
        operator = value;
      }
    } else if (value === "=") {
      if (operator !== null) {
        operand_2 = display.innerHTML;
        display.innerHTML = evaluate(operand_1, operand_2, operator);
        operand_1 = 0;
        operand_2 = null;
        operator = null;
      }
    } else if (value === ".") {
      if (parseFloat(display.innerHTML) % 1 === 0) {
        display.innerHTML += value;
      }
    } else {
      if (operator === null) {
        display.innerHTML += value;
      } else if (operand_2 === null) {
        if (operator_next === true) {
          display.innerHTML = value;
          operator_next = false;
        } else {
          display.innerHTML += value;
        }
      }
    }
  });
}
